<template>
  <div class="loading">
    <el-container class="container">
      <el-header>
        <div class="nav">
          <div class="nav-left">
            <p>vue测试</p>
          </div>
          <div class="nav-right">
            <p>登录</p>
            <p>注册</p>
          </div>
        </div>
      </el-header>
      <el-container>
        <el-aside width="200px">
          <el-menu
            default-active="1"
            class="el-menu-vertical-demo"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b">
            <el-menu-item @click="$router.push('/From')" index="1">
              <i class="el-icon-location"></i>
              <span>From</span>
            </el-menu-item>
            <el-menu-item  @click="$router.push('/Accordion')" index="2">
              <i class="el-icon-menu"></i>
              <span slot="title">Accordion</span>
            </el-menu-item>
            <el-menu-item  @click="$router.push('/Progress')" index="3">
              <i class="el-icon-document"></i>
              <span slot="title">Progress</span>
            </el-menu-item>
          </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: 'Loading',
  }
</script>


<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .container {
    height: 100%;
  }

  .el-menu-vertical-demo {
    height: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
  }

  .el-header {
    line-height: 60px;
    background: rgb(84, 92, 100);
    border-bottom: 1px solid #fff;
    color: #fff;
  }

  .nav {
    display: flex;
    justify-content: space-between;
  }

  .nav .nav-left {
    font-size: 20px;
    padding-left: 20px;
  }

  .nav .nav-right {
    display: flex;
  }

  .nav-right p {
    margin-right: 15px;
  }
</style>



